import React, {useEffect, useRef, useState} from "react";
import {useDispatch} from "react-redux";
import {SortPropertyE, SortT} from "../store/redux/filter/types";
import {setSort} from "../store/redux/filter/slice";


type PopupClick = MouseEvent & {
    path: Node[]
}
export type Sort = {
    name: string
    sortProperty: SortPropertyE
}
type SortPopupP = {
    sort: SortT
}

export const list: Sort[] = [
    {name: 'Популярности (max);', sortProperty: SortPropertyE.RATING_PLUS},
    {name: 'Популярности (min);', sortProperty: SortPropertyE.RATING_PLUS},
    {name: 'Цене (max)', sortProperty: SortPropertyE.PRICE_PLUS},
    {name: 'Цене (min);', sortProperty: SortPropertyE.PRICE_MINUS},
    {name: 'Алфавиту (z-a)', sortProperty: SortPropertyE.TITLE_PLUS},
    {name: 'Алфавиту (a-z)', sortProperty: SortPropertyE.TITLE_MINUS}
]

export const SortPopup: React.FC<SortPopupP> = React.memo(({sort}) => {

    const dispatch = useDispatch()
    const [isVisible, setIsVisible] = useState<boolean>(false)

    const sortRef = useRef<HTMLDivElement>(null)

    const changeVisible = (): void => setIsVisible(!isVisible)

    const onChangeSelected = (obj: SortT): void => {
        dispatch(setSort(obj))
        setIsVisible(false)
    }
    useEffect(() => {
        const handleClickOutside = (event: MouseEvent) => {

            const _event = event as PopupClick

            if (sortRef.current && !_event.path.includes(sortRef.current)) {
                setIsVisible(false)
            }
        }
        document.body.addEventListener('click', handleClickOutside)
        return () => document.body.removeEventListener('click', handleClickOutside)
    }, [])

    return (
        <div ref={sortRef} className="sort">
            <div className="sort__label">
                <svg
                    width="10"
                    height="6"
                    viewBox="0 0 10 6"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                >
                    <path
                        d="M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z"
                        fill="#2C2C2C"
                    />
                </svg>
                <b>Сортировка по:</b>
                <span onClick={changeVisible}>{sort.name}</span>
            </div>
            {isVisible ? <div className="sort__popup">
                <ul>
                    {list.map((obj, i) => <li onClick={() => onChangeSelected(obj)}
                                              className={sort.sortProperty === obj.sortProperty ? 'active' : ''}
                                              key={i}>{obj.name}</li>)}
                </ul>
            </div> : null}

        </div>
    )
})