export type PizzaT = {
    id: string
    title: string
    price: number
    imageUrl: string
    sizes: number[]
    types: number[]
    rating: number
}

export enum Status {
    LOADING = 'loading',
    SUCCESS = "success",
    ERROR = 'error',
}

export interface PizzaSliceState {
    items: PizzaT[]
    status: Status
}

export type SearchPizzaParams = {
    order: string
    sortBy: string
    category: string
    search: string
    currentPage: string
}
